.flex-con {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-left: 60px;
}

.heading1 {
  font-family: "Roboto";
  font-size: 50px;
  color: black;
}
.heading2 {
  font-family: "Roboto";
  font-size: 25px;
  color: orange;
  font-weight: bold;
}
.text {
  font-family: "roboto";
  font-size: 20px;
  color: gray;
  font-weight: bold;
}
.container {
  text-align: left;
  margin-left: 20px;
}
.button {
  color: black;
  background-color: orange;
  font-size: 20px;
  padding: 10px;
  border-radius: 10px;
}
.button2 {
  color: black;
  background-color: transparent;
  font-size: 20px;
  padding: 10px;
  border-radius: 10px;
  margin: 5px;
}
.line {
  opacity: 0.1;
  margin-top: 20px;
  margin-bottom: 20px;
}
.add-footer {
  background-color: yellow;
  width: 100%;
  text-decoration: underline;
  text-decoration-color: yellow;
}
@media screen and (min-width: 768px) {
  .flex-con {
    flex-direction: row;
    margin-left: 60px;
  }

  .heading1 {
    font-size: 50px;
    text-align: left;
  }

  .heading2,
  .text,
  .container {
    text-align: left;
  }

  .button,
  .button2 {
    margin: 0 5px;
  }
}
