.bg-container {
  display: flex;
  justify-content: space-between;
  color: black;
}
.main-heading {
  font-family: "roboto";
  font-size: 25px;
  color: black;
  margin-left: 80px;
}
.nav-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  background-color: #fdc435;
  width: 40.2%;
}
.nav-text {
  text-decoration: none;
  font-size: 15px;
  color: black;
  margin-top: 10px;
}

@media screen and (min-width: 768px) {
  .bg-container {
    flex-direction: row;
    justify-content: space-between;
  }

  .main-heading {
    margin-left: 0;
  }

  .nav-container {
    flex-direction: row;
    width: auto;
  }

  .nav-text {
    margin-top: 0;
    margin-right: 20px;
  }
}
